.box {
  background: #4d3210;
  width: 15%;
  height: 4rem;
  margin-bottom: 1rem; }

.pageHeader {
  color: #ffffff;
  text-shadow: 2px 2px 3px #4d3210;
  padding-left: 2rem;
  padding-top: 1rem; }
  @media screen and (min-width: 768px) {
    .pageHeader {
      padding-left: 4rem; } }

.forthPage {
  height: 100vh;
  position: relative; }

.aboutBox {
  position: absolute;
  top: 10rem;
  right: 1rem;
  background: #c77171;
  width: 40%;
  height: 20%;
  z-index: 300;
  -webkit-transition: opacity 1s linear;
  -o-transition: opacity 1s linear;
  transition: opacity 1s linear;
  opacity: 1; }
  @media screen and (min-width: 1000px) {
    .aboutBox {
      right: 5rem;
      top: 18rem;
      width: 30%; } }

.aboutImage {
  position: relative;
  top: 1rem;
  width: 100%; }
  @media screen and (min-width: 1000px) {
    .aboutImage {
      width: 70%; } }
  @media screen and (min-width: 1100px) {
    .aboutImage {
      width: 60%; } }

.aboutImageShade {
  position: absolute;
  top: 6rem;
  width: 100%;
  height: 20%;
  background: -webkit-gradient(linear, left bottom, left top, from(rgba(255, 255, 255, 0)), to(#ffffff));
  background: -webkit-linear-gradient(bottom, rgba(255, 255, 255, 0), #ffffff);
  background: -o-linear-gradient(bottom, rgba(255, 255, 255, 0), #ffffff);
  background: linear-gradient(to top, rgba(255, 255, 255, 0), #ffffff); }
  @media screen and (min-width: 665px) {
    .aboutImageShade {
      height: 55%; } }
  @media screen and (min-width: 1000px) {
    .aboutImageShade {
      bottom: 1rem;
      height: 75%;
      background: -webkit-gradient(linear, left top, right top, from(rgba(255, 255, 255, 0)), color-stop(#ffffff), to(#ffffff));
      background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0), #ffffff, #ffffff);
      background: -o-linear-gradient(left, rgba(255, 255, 255, 0), #ffffff, #ffffff);
      background: linear-gradient(to right, rgba(255, 255, 255, 0), #ffffff, #ffffff); } }
  @media screen and (min-width: 1100px) {
    .aboutImageShade {
      height: 90%; } }

.aboutText {
  position: absolute;
  top: 10rem;
  padding: 0% 10%;
  font-size: 2rem;
  color: #ffffff;
  text-shadow: 2px 2px 3px #000000;
  -webkit-transition: opacity 1s linear;
  -o-transition: opacity 1s linear;
  transition: opacity 1s linear;
  opacity: 1;
  z-index: 350; }
  @media screen and (min-width: 1000px) {
    .aboutText {
      font-size: 3rem;
      top: 18rem; } }

.contactContainer {
  width: 100%;
  text-align: center;
  margin-top: 4rem; }
  @media screen and (min-width: 1100px), (orientation: landscape) {
    .contactContainer {
      text-align: left;
      padding: 2rem 0 2rem 2rem;
      margin-top: 6rem; } }



